.wd-row {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(12, 1fr);
}

.wd-row > * {
  grid-column-end: span 12;
}

@media (min-width: 768px) {
  .wd-row {
    gap: 32px;
  }
}

